.app__about {
  flex: 1;
  width: 100%;
  flex-direction: column;
}
.head-text-span{
  color: rgb(0, 225, 255);
}

.app__profiles {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 2rem;
background: rgba(255, 255, 255, 0.144);
border-radius: 16px;
backdrop-filter: blur(2.4px);
-webkit-backdrop-filter: blur(2.4px);

  h2{
    color: rgb(0, 225, 255);
  }
}

.app__profile-item {
  width: 190px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin: 2rem;
    p{
      font-size: 0.8rem;
      text-align: left;
      color: white;
      line-height: 1.5;
    
      @media screen and (min-width: 2000px) {
        font-size: 1.75rem;
      }
    }
  

  img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    object-fit: cover;
  }

  @media screen and (min-width: 2000px) {
    width: 370px;
    margin: 2rem 4rem;

    img {
      height: 320px;
    }
  }
}
